#contact p {
  margin: 20px 0;
}
#contact input,
#contact textarea {
  border: 2px solid #666;
  font-family: 'VG';
}
#contact button.ui.button.blue {
  font-family: 'VG';
  width: 100%;
  margin-bottom: 10px;
}
#contact .ui.positive.message {
  background-color: #21ba45;
}
#contact .ui.negative.message {
  background-color: #db2828;
}
#contact .ui.message .header {
  color: white;
  font-family: 'VG';
}
div#sending-mail {
  height: 100px;
  text-align: center;
  font-size: 40px;
  padding: 20px;
  margin: 40px;
}
i.spinner.icon {
  animation: sending 1s linear infinite;
  height: 80px;
  width: 80px;
  padding-top: 30px;
}
@keyframes sending {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
@media screen and (max-width: 768px) {
  #contact input,
  #contact textarea {
    font-size: 12px;
  }
}
